@keyframes bounceIn {
    0% {transform: translateY(50px); opacity: 0;}
    100% {opacity: 1; transform: translateY(0px);}
}

@keyframes fadeInTop {
    0% {transform: translateY(-20px); opacity: 0;}
    100% {opacity: 1; transform: translateY(0px);}
}

@keyframes glow {
    0% {text-shadow: 0 0 0; transform: scale(1);}
    20% {text-shadow: 0 0 20px; transform: scale(0.9);}
    100% {text-shadow: 0 0 0; transform: scale(1);}
}

@keyframes loading {
    0% {transform: translateX(-100%);}
    100% {transform: translateX(100%);}
}

@keyframes fadeOut {
    from {opacity: 1;}
    to {opacity: 0;}
}

@keyframes fadeIn {
    from {opacity: 0;}
    to {opacity: 1;}
}


@keyframes touchActionTraits {
    0% {transform: scale(0.8); opacity: 0;}
    10% {transform: scale(0.8); opacity: 0;}
    80% {opacity: 1;}
    100% {transform: scale(1.3); opacity: 0;}
}

@keyframes touchActionHand {
    0% {transform: scale(1);}
    25% {transform: scale(0.8);}
    50% {transform: scale(1);}
    100% {transform: scale(1);}
}

@keyframes touchActionHandLittle {
    0% {transform: scale(1);}
    25% {transform: scale(0.9);}
    50% {transform: scale(1);}
    100% {transform: scale(1);}
}

@keyframes spin {
    0% {transform: rotate(0deg);}
    100% {transform: rotate(180deg);}
}

@keyframes spinFull {
    0% {transform: rotate(0deg);}
    100% {transform: rotate(360deg);}
}


// Section transitions

@keyframes animationChangeListener {
    to {opacity: 1;}
}

@keyframes animationEndListener {
    from {opacity: 1;}
    to {opacity: 0;}
}

@keyframes sectionHide {
    to {
        transform: translateX(10%) skew($t-skew);
    }
}

@keyframes sectionShow {
    from {
        transform: translateX(10%) skew($t-skew);
    }

    to {
        transform: translateX(150%) skew($t-skew);
    }
}